.gpt3__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
}

.gpt3__navbar-links{
    display: flex;
    align-items: center;
}

.gpt3__navbar-links_logo{
    margin-left: 3rem;
}

.gpt3__navbar-links_logo img{
    width: 62.56px;
    height: 16px;
}

.gpt3__navbar-links_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0rem 3rem;
}

.gpt3__navbar-links_container > p {
    margin: 0rem 1.4rem;
    font-family: var(--font-family);
    font-weight: 500;
    color: #fff;
    font-size: 16px;
    line-height: 25px;
    text-transform: capitalize;
    cursor: pointer;
}

a {
    color: #fff;
    text-decoration: none;
}

a:hover {
    color: #ffffffaa;
    text-decoration: underline;
}

.gpt3__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 3rem;
}

.gpt3__navbar-sign button{
    background: #FF4820;
    border-style: none;
    border-radius: 5px;
    font-size: 18px;
    padding: 0.8rem 2.6rem;
    /* width: 138px; */
    /* height: 48px; */
    cursor: pointer;
}

.gpt3__navbar-sign p{
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: #FFFFFF;
    margin: auto 1rem;
    /* margin-right: 1rem; */
    cursor: pointer;
}

.gpt3__navbar-menu{
    display: none;
}

/* .gpt3__navbar-menu-container{
    display: flex;
} */

.gpt3__navbar-menu-container-links{
    background: var(--color-footer);
    font-family: var(--font-family);
    position: absolute;
    top: 2rem;
    right: 0.4rem;
    width: 8rem;
    padding: 8px 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
}

.gpt3__navbar-menu-container-links p{
    margin: 0.2rem 0rem;
    cursor: pointer;
}

.gpt3__navbar-menu-container-links-sign{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
}

.gpt3__navbar-menu-container-links-sign button{
        background: #FF4820;
        border-style: none;
        border-radius: 5px;
        font-size: 16px;
        width: 86px;
        height: 30px;
        cursor: pointer;
        margin-top: 0.3rem;
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-sign{
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    .gpt3__navbar-menu{
        display: block; 
    }
    .gpt3__navbar-menu-container-links-sign{
        display: none;
    }
}

@media screen and (max-width:700px) {
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-sign{
        display: none;
    }
    .gpt3__navbar-menu{
        display: block;
    }   
    .gpt3__navbar-menu-container-links-sign{
        display: contents;
    }
}

@media screen and (max-width:500px) {
    .gpt3__navbar-links_logo{
        margin-left: 1rem;
    }
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-sign{
        display: none;
    }
    .gpt3__navbar-menu{
        display: block;
    }
}
