.gpt3__footer-content{
    display: flex;
    flex-direction: column;
    margin-top: 4rem;
}

.gpt3__footer-content_access {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 4rem;
}

.gpt3__footer-content_container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 4rem;
}

.gpt3__footer-content_container-2 {
    display: flex;
    justify-content: space-between;
    width: 40vw;
}

.gpt3__footer-content_rights {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.gpt3__footer-content_access > h1 {
    width: 65%;
    text-align: center;
    font-size: 62px;
    font-family: Manrope;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
    margin-bottom: 3rem;
}

.gpt3__footer-content_access > button {
    color: #FFF;
    font-size: 18px;
    font-family: Gilroy-Medium;
    border: 1px solid #FFF;
    background: var(--color-bg);
    font-family: var(--font-family);
    padding: 6px 12px;
    cursor: pointer;
}

.gpt3__footer-content_container-1 > h1 {
    font-weight: 700;
}

.gpt3__footer-content_container-1 > p {
    width: 185px;
    font-size: 12px;
    line-height: 18px;
    margin-top: 0.5rem;
}

.gpt3__footer-content_container-2 p {
    font-size: 14px;
    font-family: 'Gilroy-Medium';
    width: 116px;
    line-height: 16px;
    cursor: pointer;
}

.gpt3__footer-content_container-2_element1 p:first-child{
    color: #FFF;
    font-size: 16px;
    font-weight: 600;
}
.gpt3__footer-content_container-2_element2 p:first-child{
    color: #FFF;
    font-size: 16px;
    font-weight: 600;
}
.gpt3__footer-content_container-2_element3 p:first-child{
    color: #FFF;
    font-size: 16px;
    font-weight: 600;
}

footer {
    color: #FFF;
    font-size: 16px;
    font-family: monospace;
    padding: 1rem;
}

@media screen and (max-width:770px) {
    .gpt3__footer-content_access > h1 {
        width: 85%;
        font-size: 54px;
        line-height: 65px;
        margin-bottom: 3rem;
    }
}

@media screen and (max-width:450px) {
    .gpt3__footer-content_access > button {
        font-size: 14px;
        padding: 6px 12px;
    }
    .gpt3__footer-content_container {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 2rem auto;
    }
    .gpt3__footer-content_container-1 {
        display: flex;
        justify-content: space-between;
        width: 85vw;
        margin-bottom: 2rem;
    }
    .gpt3__footer-content_container-2 {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: auto;
        width: 85vw;
    }
    .gpt3__footer-content_access > h1 {
        width: 85%;
        font-size: 40px;
        line-height: 50px;
        margin-bottom: 3rem;
    }
    .gpt3__footer-content_container-1 > p {
        width: 185px;
        font-size: 10px;
        line-height: 16px;
        margin-top: 0.5rem;
    }
    .gpt3__footer-content_container-2 p {
        font-size: 10px;
        font-family: 'Gilroy-Medium';
        width: 116px;
        line-height: 16px;
        cursor: pointer;
    }
    footer {
        font-size: 12px;
    }
}