.gpt3__blog-content {
    display: flex;
    flex-direction: column;
    padding: 15px 3vw;
    margin: auto 3vw;
}

.gpt3__blog-content_article {
    display: flex;
}

.gpt3__blog-content > h1 {
    width: 70vw;
    font-size: 62px;
    font-family: Manrope;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
    margin-bottom: 3rem;
}

.gpt3__blog-content_article-part1 {
    flex: 0.65;
    margin-right: 1rem;
}

.gpt3__blog-content_article-part2 {
    flex: 1;

    display: grid;
    grid-template-columns: repeat(2, 1fr);

    column-gap: 1rem;
    row-gap: 1rem;

    width: 100%;
    height: 100%;
}

@media screen and (max-width:770px) {
    .gpt3__blog-content > h1 {
        width: 82vw;
        font-size: 50px;
        line-height: 65px;
    }
    .gpt3__blog-content_article-part2 > div > div > div > h2 {
        font-size: 16px;
    }
    .gpt3__blog-content_article-part2 div div p {
        font-size: 10px;
    }
}

@media screen and (max-width: 450px) {
    .gpt3__blog-content {
        margin-top: 2rem;
    }

    .gpt3__blog-content_article {
        display: flex;
        flex-direction: column-reverse;
    }
    .gpt3__blog-content > h1 {
        width: 82vw;
        font-size: 26px;
        line-height: 30px;
    }
    .gpt3__blog-content_article-part1 {
        margin: auto;
    }
    .gpt3__blog-content_article-part1 > div {
        margin-top: 7px;
    }
    
    .gpt3__blog-content_article-part1 > div > div > div > h2 {
        font-size: 14px;
    }
    .gpt3__blog-content_article-part2 > div > div > div > h2 {
        font-size: 12px;
    }
    .gpt3__blog-content_article-part2 div div p {
        font-size: 10px;
    }
    .gpt3__blog-content_article-part2 {
        column-gap: 7px;
        row-gap: 7px;
    }
}