.gpt3__wgpt-content-part1 > div {
    flex-direction: row;
    justify-content: space-between;
}

.gpt3__wgpt-content-part1 > div > div > h2{
    /* color: bisque; */
    width: max-content;
    padding-right: 1rem;
}

.gpt3__wgpt-content-part1 > div > div > p{
    width: 56vw;
    margin: 1rem 0.5rem;
}

.gpt3__wgpt-content{
    background: var(--color-footer);
    margin: 1rem 2rem;
    padding: 2rem 2rem;
}

.gpt3__wgpt-content-part2{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0rem;
}

.gpt3__wgpt-content-part2 h1 {
    width: 500px;
    color: #FFF;
    font-size: 34px;
    font-family: Manrope;
    font-weight: 800;
    line-height: 45px;
}

.gpt3__wgpt-content-part2 p {
    color: var(--color-subtext);
    font-size: 16px;
    font-family: Manrope;
    font-weight: 500;
    line-height: 30px;
}

.gpt3__wgpt-content-part3{
    display: flex;
    justify-content: space-between;
}

.gpt3__wgpt-content-part3 > div {
    width: 333px;
}

@media screen and (max-width:770px) {
    .gpt3__wgpt-content-part2 h1 {
        width: 400px;
        font-size: 30px;
        font-weight: 800;
        line-height: 35px;
    }
    
    .gpt3__wgpt-content-part2 p {
        font-size: 14px;
        font-weight: 300;
        line-height: 30px;
    }
    p{
        font-size: 14px;
    }
    .gpt3__wgpt-content-part3 > div {
        width: 180px;
    }
    .gpt3__wgpt-content-part1 > div > div > p{
        width: 50vw;
    }
}

@media screen and (max-width:450px) {
    .gpt3__wgpt-content{
        margin: 1rem 1.5rem;
        padding: 1rem 1rem;
    }
    .gpt3__wgpt-content-part1 > div {
        flex-direction: column;
        justify-content: space-between;
        width: fit-content;
    }
    .gpt3__wgpt-content-part1 > div > div > p{
        width: fit-content;
        margin: 1rem auto;
    }
    .gpt3__wgpt-content-part2{
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
    }
    .gpt3__wgpt-content-part2 h1 {
        width: fit-content;
        font-size: 24px;
        font-weight: 800;
        line-height: 35px;
    }
    .gpt3__wgpt-content-part2 p {
        font-size: 14px;
        font-weight: 300;
        line-height: 30px;
    }
    p{
        font-size: 12px;
        line-height: 20px;
        margin-top: 0.7rem;
    }
    .gpt3__wgpt-content-part3{
        flex-direction: column;
        justify-content: space-between;
    }
    .gpt3__wgpt-content-part3 > div {
        width: fit-content;
        margin: 0.7rem 0rem;
    }
}