.gpt3__possibility-content{
    display: flex;
    align-items: flex-end;
    width: 75vw;
    margin: -3rem auto 2rem auto;
}

.gpt3__possibility-content_image img {
    width: 100%;
    height: 100%;
}

#p-tag {
    color: var(--color-subtext);
}

.gpt3__possibility-content_information {
    padding: 2rem 0rem;
}

.gpt3__possibility-content_information > h2 {
    color: #FFF;
    font-size: 32px;
    font-family: Manrope;
    font-weight: 800;
    line-height: 45px;
}

.gpt3__possibility-content_information > p {
    color: #81AFDD;
    font-size: 16px;
    font-family: Manrope;
    line-height: 30px;
}

@media screen and (max-width:770px) {
    .gpt3__possibility-content_information > h2 {
        margin-top: 5px;
        font-size: 22px;
        line-height: 26px;
    }   
    .gpt3__possibility-content_information > p {
        font-size: 12px;
        line-height: 15px;
    }
}

@media screen and (max-width:450px) {
    .gpt3__possibility-content{
        flex-direction: column;
        margin-top: -1rem;
        width: 90vw;
    }
    .gpt3__possibility-content_image {
        display: flex;
        justify-content: center;
        margin-top: -1rem;
    }
    .gpt3__possibility-content_image img {
        width: 85%;
    }
    .gpt3__possibility-content_information {
        margin: -1.3rem auto;
    }
    .gpt3__possibility-content_information > h2 {
        margin-top: 5px;
        font-size: 18px;
        line-height: 24px;
    }   
    .gpt3__possibility-content_information > p {
        font-size: 10px;
        line-height: 14px;
    }
}