.gpt3__brand{
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__brand-content{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}

.gpt3__brand-content > img{
    padding: 2rem 0rem;
    margin: 1rem 1.4rem;
}

@media screen and (max-width: 450px) {
    .gpt3__brand-content{
        flex-direction: row;
        flex-wrap: wrap;

    }
    .gpt3__brand-content > img{
        width: 50px;
        padding: 0.5rem 0rem;
        margin: 0rem 0.5rem;
    }
}

@media screen and (max-width: 600px) {
    .gpt3__brand-content{
        flex-direction: row;
        flex-wrap: wrap;

    }
    .gpt3__brand-content > img{
        width: 50px;
        padding: 0.5rem 0rem;
        margin: 0rem 0.5rem;
    }
}