.gpt3__features-content{
    display: flex;
    padding: 5rem 5rem;
    justify-content: space-between;
}

.gpt3__features-content_part1 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 30vw;
}

.gpt3__features-content_part2 > div {
    display: flex;
    flex-direction: row;
    /* align-items: center; */
    justify-content: space-between;
    width: 47vw;
    margin-bottom: 2rem;
}

.gpt3__features-content_part2 h2 {
    color: #FFF;
    font-size: 18px;
    font-family: Manrope;
    font-weight: 800;
    line-height: 24px;
    letter-spacing: -0.72px;
    width: 15vw;
}

.gpt3__features-content_part2 p {
    color: #81AFDD;
    font-size: 14px;
    font-family: Manrope;
    font-weight: 500;
    line-height: 24px;
    width: 30vw;
    margin-top: 0px;
}

.gpt3__features-content_part2 > div > div > div {
    margin-bottom: 7px;
}

@media screen and (max-width:770px) {
    .gpt3__features-content{
        padding: 2rem 3rem;
    }
    .gpt3__features-content_part1 h1 {
        font-size: 22px;
        margin-bottom: 2rem;
    }
    .gpt3__features-content_part1 p {
        font-size: 11px;
    }
    .gpt3__features-content_part2 > div {
        width: 48vw;
        margin-bottom: 1rem;
    }
    .gpt3__features-content_part2 h2 {
        font-size: 14px;
    }
    .gpt3__features-content_part2 p {
        font-size: 11px;
    }
}

@media screen and (max-width:450px) {
    .gpt3__features-content{
        display: flex;
        flex-direction: column;
        padding: 2rem 2rem;
        /* justify-content: space-between; */
    }
    .gpt3__features-content_part1 {
        width: fit-content;
        margin-bottom: 20px;
    }
    .gpt3__features-content_part2 > div > div > div {
        margin-bottom: 2px;
    }
    .gpt3__features-content_part1 h1 {
        font-size: 18px;
        margin-bottom: 0rem;
    }
    .gpt3__features-content_part1 p {
        font-size: 10px;
    }
    .gpt3__features-content_part2 > div {
        flex-direction: column;
        min-width: 90vw;
        justify-content: center;
    }
    .gpt3__features-content_part2 h2 {
        font-size: 12px;
        width: max-content;
    }
    .gpt3__features-content_part2 p {
        font-size: 10px;
        width: 85vw;
    }
}