.gpt3__cta-content {
    background: var(--gradient-bar);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 3rem;
    margin: 6rem 5vw;
    border-radius: 10px;
}

.gpt3__cta-content_text {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 10px 0px;
}
.gpt3__cta-content_text > p {
    color: #0E0E0E;
    font-size: 12px;
    font-family: Manrope;
    font-weight: 500;
    line-height: 30px;
    margin-top: 0rem;
}

.gpt3__cta-content_text > h3 {
    color: #000;
    font-size: 24px;
    font-family: Manrope;
    font-weight: 800;
    line-height: 45px;
}

.gpt3__cta-content_button > button {
    border-radius: 40px;
    background: #000;
    color: #FFF;
    font-size: 18px;
    font-family: Manrope;
    font-weight: 700;
    line-height: 30px;
    padding: 4px 18px;
    border: none;
    cursor: pointer;
}

@media screen and (max-width:770px) {
    .gpt3__cta-content{
        padding: 1rem 2rem;
    }
    .gpt3__cta-content_text > p {
        font-size: 12px;
        line-height: 24px;
        margin-top: 0rem;
    }
    
    .gpt3__cta-content_text > h3 {
        font-size: 18px;
        line-height: 26px;
    }
    
    .gpt3__cta-content_button > button {
        border-radius: 35px;
        font-size: 16px;
        line-height: 20px;
        padding: 5px 20px;
        }
}

@media screen and (max-width:450px) {
    .gpt3__cta-content{
        padding: 1rem 1rem;
        flex-direction: column;
        margin: 0rem 5vw;

    }
    .gpt3__cta-content_text > p {
        font-size: 10px;
        line-height: 22px;
        margin-top: 0rem;
    }
    
    .gpt3__cta-content_text > h3 {
        font-size: 16px;
        line-height: 24px;
    }
    
    .gpt3__cta-content_button > button {
        border-radius: 35px;
        font-size: 12px;
        line-height: 18px;
        padding: 5px 20px;
        }
}