.gpt3__header{
    /* margin-top: 0.6rem; */
    margin-left: 2.4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 35px 0px;
}

.gpt3__header-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 614px;
}

.gpt3__header-content h1{
    font-size: 62px;
    font-family: Manrope;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
    margin-bottom: 1rem;
}

.gpt3__header-content > p{
    color: #81AFDD;
    font-size: 20px;
    font-family: Manrope;
    margin-bottom: 2rem;
}

.gpt3__header-content_people{
    display: flex;
    align-items: center;
}

.gpt3__header-content_input input{
    border-radius: 5px 0px 0px 5px;
    background: #052D56;
    border: none;
    padding-left: 2rem;
    width: 425px;
    height: 70px;
    margin-bottom: 2rem;
    color: #3D6184;
    font-size: 20px;
    font-family: var(--font-family);
    outline: none;
}

.gpt3__header-content_input button{
    border-radius: 5px 0px 0px 5px;
    background: #FF4820;
    width: 174px;
    height: 70px;
    border: none;
    color: #FFF;
    font-size: 20px;
    font-family: Manrope;
    font-weight: 700;
}

.gpt3__header-content_people img{
    margin-right: 10px;
}
.gpt3__header-content_people p{
    margin-top: 0rem;
}

.gpt3__header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img{
    width: 100%;
    height: 100%;
}

@media screen and (max-width:1050px) {
    .gpt3__header-content h1{
        font-size: 52px;
        line-height: 60px;
    }
    .gpt3__header-content > p{
        font-size: 16px;
        margin-bottom: 1.5rem;
    }
    .gpt3__header-content_input input{
        padding-left: 2rem;
        width: 330px;
        height: 56px;
        margin-bottom: 1.5rem;
        font-size: 17px;
    }
    
    .gpt3__header-content_input button{
        width: 120px;
        height: 56px;
        font-size: 17px;
        cursor: pointer;
    }
    .gpt3__header-content_people > p {
        font-size: 14px;
    }
}

@media screen and (max-width:850px) {
    .gpt3__header{
        display: flex;
        flex-direction: column;
        margin: 1rem;
    }
    .gpt3__header-image img{
        width: 100%;
        height: 100%;
    }
    .gpt3__header-content{
        width: 690px;
    }
    .gpt3__header-content h1{
        font-size: 52px;
        line-height: 60px;
    }
    .gpt3__header-content > p{
        font-size: 16px;
        margin-bottom: 1.5rem;
    }
    .gpt3__header-content_input input{
        padding-left: 2rem;
        width: 430px;
        height: 56px;
        margin-bottom: 1.5rem;
        font-size: 17px;
    }
    
    .gpt3__header-content_input button{
        width: 125px;
        height: 56px;
        font-size: 17px;
    }
    .gpt3__header-content_people{
        margin-bottom: 1.5rem;
    }
    .gpt3__header-content_people > p {
        font-size: 14px;
    }
}

@media screen and (max-width:450px) {
    .section__padding{
        padding: 1rem;
    }
    .gpt3__header{
        /* padding: 0px; */
        margin: auto;
        width: 100vw;
    }
    .gpt3__header-content{
        width: 90vw;
    }
    .gpt3__header-content h1{
        font-size: 40px;
        line-height: 45px;
    }
    .gpt3__header-content > p{
        font-size: 12px;
        margin-bottom: 1.5rem;
    }
    .gpt3__header-content_input{
        display: flex;
        justify-content: center;
    }
    .gpt3__header-content_input input{
        /* padding-left: 2rem; */
        width: 55vw;
        height: 36px;
        /* margin-bottom: 1.5rem; */
        font-size: 0.7rem;
    }
    
    .gpt3__header-content_input button{
        width: 23vw;
        height: 36px;
        font-size: 0.7rem;
    }
    .gpt3__header-content_people{
        display: flex;
        flex-direction: column;
    }
    .gpt3__header-content_people img{
        margin-bottom: 5px;
    }
    .gpt3__header-content_people p{
        font-size: 12px;
    }
}

@media screen and (max-width: 550px){
    .section__padding {
        padding: 1rem 1rem;
    }
}

