*{
    font-family: var(--font-family);
}

p {
    color: #81AFDD;
    font-size: 16px;
    font-family: Manrope;
    font-weight: 500;
    line-height: 35px;
    margin-top: 2rem;
}

.content__div-title div{
    width: 38px;
    height: 3px;
    flex-shrink: 0;
    background: linear-gradient(143deg, #AE67FA 0%, #F49867 97.40%), #FFF;
    margin-bottom: 0.8rem;
}

.content__div-title h2{
    color: #FFF;
    font-size: 24px;
    font-family: Manrope;
    font-weight: 800;
    /* line-height: 55px; */
    letter-spacing: -0.96px;
}

.content__div{
    display: flex;
    flex-direction: column;
}

@media screen and (max-width:770px) {
    .content__div-title h2{
        font-size: 20px;
        font-weight: 800;
    }
}

@media screen and (max-width:450px) {
    .content__div-title div{
        margin-bottom: 0.4rem;
    }
    .content__div-title h2{
        font-size: 16px;
        font-weight: 800;
    }
}