.gpt3__article {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding-bottom: 10px;
    background: var(--color-footer);
}

.gpt3__article > img {
    width: 100%;
    height: 100%;
}

.gpt3__article > div {
    width: 100%;
    height: 100%;
}

.gpt3__article > h2 {
    width: 100%;
    height: 100%;
    word-wrap: break-word;
    width: inherit;
}

.gpt3__article-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0rem 5px;
}

.gpt3__article-info > div {
    margin-bottom: 25px;
}

.gpt3__article-info p {
    margin-top: 0rem;
    cursor: pointer;
}